
import XHeader from 'vux/src/components/x-header/index.vue'

import Collapse from '@/components/Collapse/Collapse'
import ShowMore from '@/components/ShowMore/showMore'
export default{
components: {
XHeader,Collapse,ShowMore
},
data(){
return {
msg:"",
pageTitle:"",
info:{
GRXM:"",
CSFBH:"",
SFRQ:"",
SFFSMC:"",
IZLYX:"",
IDDRY:"",
CZZMC:"",
CHLFA:"",
IYF:"",
CYPJXMC:"",
ILFYCS:"",
YWYWBLFYMC:"",
CBFZHHBZ:"",
IXYL:"",
IYJL:"",
CZZKB:"",
CZZYY:"",
CLZNSFJG:"",
CCLYJ:"",
DXCSFCJSJ:"",
FSRXM:"",
DTZZLRQ:"",
CTZZLYY:"",
IYFFHZCS:"",
ISJFSCS:"",
IYFYCS:"",
ISJFYCS:"",
NFYL:"",
FSRXM:"",
DDFSMC:""
}
}
},
mounted(){
if(this.$route.params.type == "FJH"){
this.msg = "qryJHBInfo";
this.pageTitle = "随访服务记录";
}else{
this.msg = "qryJHBFirstInfo";
this.pageTitle = "第一次入户随访记录";
}
this.getInfo();
},
methods: {
getInfo(){

this.$ajax.post('',{
'MSH.1':'zbsf',
'MSH.2':this.msg,
'SFJLID':this.$route.params.SFJLID,//精神病随访ID
'YLJGID':this.$route.params.YLJGID,//医疗机构ID
'GLKID':this.$route.params.GLKID,//管理卡ID
'token':this.$root.loginInfo.token,
}).then(res => {
if(res.info){
this.info = res.info;
}else{
this.info = res;
}
}).catch(error => {
console.log(error);

});
},
getIDDRYMC(num) {
switch (num) {
case 1:
return "医生";
break;
case 2:
return "家属";
break;
case 3:
return "自服药";
break;
case 4:
return "其他";
break;
default:
return "";
break;
}
},
getIFYMC(num) {
switch (num) {
case 1:
return "每日";
break;
case 2:
return "间隔";
break;
default:
return "";
break;
}
},
getIDDJSMC(num) {
switch (num) {
case 1:
return "有";
break;
case 2:
return "无";
break;
default:
return "";
break;
}
},
getITFQKMC(num) {
switch (num) {
case 1:
return "良好";
break;
case 2:
return "一般";
break;
case 3:
return "差";
break;
default:
return "";
break;
}
},
getTransformMC(num) {
switch (num) {
case 1:
return "掌握";
break;
case 2:
return "未掌握";
break;
default:
return "";
break;
}
},
}
}




